import fs from 'fs';
import path from 'path';
import matter from 'gray-matter';
import { remark } from 'remark';
import html from 'remark-html';
import { Post } from '@/types/post';

const postsDirectory = path.join(process.cwd(), 'posts');
const sectionsDirectory = path.join(process.cwd(), 'sections');

export function getAllPosts(): Post[] {
  // Obtener nombres de archivos de la carpeta posts
  const fileNames = fs.readdirSync(postsDirectory);
  
  const allPostsData = fileNames
    .filter((fileName) => fileName.endsWith('.md'))
    .map((fileName) => {
      // Remover ".md" del nombre del archivo para obtener el slug
      const slug = fileName.replace(/\.md$/, '');

      // Leer el archivo markdown como string
      const fullPath = path.join(postsDirectory, fileName);
      const fileContents = fs.readFileSync(fullPath, 'utf8');

      // Usar gray-matter para parsear el frontmatter del post
      const matterResult = matter(fileContents);

      // Combinar los datos con el slug
      return {
        slug,
        title: matterResult.data.title,
        date: matterResult.data.date,
        content: matterResult.content,
        excerpt: matterResult.data.excerpt,
      };
    });

  // Ordenar posts por fecha y hora de creación (más recientes primero)
  return allPostsData.sort((a, b) => {
    const dateA = new Date(a.date);
    const dateB = new Date(b.date);
    
    if (dateA < dateB) {
      return 1;
    } else if (dateA > dateB) {
      return -1;
    } else {
      return 0;
    }
  });
}

export function getPostsWithPagination(page: number = 1, limit: number = 8): { posts: Post[], totalPosts: number, totalPages: number, currentPage: number } {
  const allPosts = getAllPosts();
  const totalPosts = allPosts.length;
  const totalPages = Math.ceil(totalPosts / limit);
  const startIndex = (page - 1) * limit;
  const endIndex = startIndex + limit;
  const posts = allPosts.slice(startIndex, endIndex);

  return {
    posts,
    totalPosts,
    totalPages,
    currentPage: page
  };
}

export function getPostBySlug(slug: string): Post | null {
  try {
    const fullPath = path.join(postsDirectory, `${slug}.md`);
    const fileContents = fs.readFileSync(fullPath, 'utf8');

    // Usar gray-matter para parsear el frontmatter del post
    const matterResult = matter(fileContents);

    // Usar remark para convertir markdown a HTML
    const processedContent = remark()
      .use(html)
      .processSync(matterResult.content);
    const contentHtml = processedContent.toString();

    // Combinar los datos con el slug y el contenido HTML
    return {
      slug,
      title: matterResult.data.title,
      date: matterResult.data.date,
      content: contentHtml,
      excerpt: matterResult.data.excerpt,
    };
  } catch {
    return null;
  }
}

export function getAllPostSlugs(): string[] {
  const fileNames = fs.readdirSync(postsDirectory);
  return fileNames
    .filter((fileName) => fileName.endsWith('.md'))
    .map((fileName) => fileName.replace(/\.md$/, ''));
}

// Section functions
export function getSectionBySlug(slug: string): { title: string; content: string } | null {
  try {
    const fullPath = path.join(sectionsDirectory, `${slug}.md`);
    const fileContents = fs.readFileSync(fullPath, 'utf8');

    // Usar gray-matter para parsear el frontmatter del post
    const matterResult = matter(fileContents);

    // Usar remark para convertir markdown a HTML
    const processedContent = remark()
      .use(html)
      .processSync(matterResult.content);
    const contentHtml = processedContent.toString();

    // Combinar los datos con el slug y el contenido HTML
    return {
      title: matterResult.data.title,
      content: contentHtml,
    };
  } catch {
    return null;
  }
} 