import Link from 'next/link';
import { notFound } from 'next/navigation';
import { getPostBySlug, getAllPostSlugs } from '@/lib/posts';
import { format } from 'date-fns';
import { enUS } from 'date-fns/locale';

interface PostPageProps {
  params: Promise<{
    slug: string;
  }>;
}

export async function generateStaticParams() {
  const slugs = getAllPostSlugs();
  return slugs.map((slug) => ({
    slug: slug,
  }));
}

export default async function PostPage({ params }: PostPageProps) {
  const post = getPostBySlug((await params).slug);
  const fromPage = '1'; // Para exportación estática, siempre volver a página 1

  if (!post) {
    notFound();
  }

  return (
    <div className="min-h-screen" style={{ background: '#ffffff' }}>
      <div className="w-full mx-auto px-6 py-12">
        {/* Back to posts */}
        <Link 
          href={`/?page=${fromPage}`}
          className="inline-flex items-center mb-8 text-sm transition-colors"
          style={{ color: '#059669' }}
        >
          <svg className="mr-2 h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
          Back to posts
        </Link>

        {/* Post Header */}
        <header className="mb-12">
          <h1 className="text-3xl font-bold mb-4 leading-tight" style={{ color: '#000000' }}>
            {post.title}
          </h1>
          <time className="text-sm" style={{ color: '#666666' }}>
            {format(new Date(post.date), 'dd MMMM yyyy', { locale: enUS })}
          </time>
        </header>

        {/* Post Content */}
        <article className="prose prose-lg" style={{ 
          backgroundColor: '#f8f9fa', 
          padding: '1.5rem', 
          borderRadius: '0.25rem',
          border: '1px solid #e1e5e9'
        }}>
          <div dangerouslySetInnerHTML={{ __html: post.content }} />
        </article>

        {/* Back to posts link */}
        <div className="mt-16 pt-8" style={{ borderTop: '1px solid #e1e5e9' }}>
          <Link 
            href={`/?page=${fromPage}`}
            className="inline-flex items-center text-sm transition-colors"
            style={{ color: '#059669' }}
          >
            <svg className="mr-2 h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
            Back to posts
          </Link>
        </div>

        {/* Footer */}
        <footer className="mt-24 pt-8" style={{ borderTop: '1px solid #e1e5e9' }}>
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center">
            <p className="text-sm mb-4 md:mb-0" style={{ color: '#666666' }}>
              © 2004-{new Date().getFullYear()} shidare.com. Made with Next.js and Markdown.
            </p>
          </div>
        </footer>
      </div>
    </div>
  );
} 